<template>
	<view>
		<!-- 菜单 -->
		<view class="top-warp">
			<image src="../../static/images/all-award-bg.png" mode="aspectFill"></image>
			<view class="nav">
				<view :class="{ active: tabType == 0 }" @click="changeTab(0)">正在进行中</view>
				<view :class="{ active: tabType == 1 }" @click="changeTab(1)">已结束</view>
			</view>
		</view>

		<!-- top="xxx"下拉布局往下偏移,防止被悬浮菜单遮住 -->
		<mescroll-uni top="120" :up="upOption" @up="upCallback" @emptyclick="emptyClick" @init="mescrollInit">
			<!-- 数据列表 -->
			<pd-list :list="pdList" :isShowFinish="isShowFinish" ></pd-list>
		</mescroll-uni>
	</view>
</template>

<script>
import MescrollUni from '@/components/mescroll-uni/mescroll-uni.vue';
import PdList from '@/components/pd-list.vue';

export default {
	components: {
		MescrollUni,
		PdList
	},
	data() {
		return {
			mescroll: null, //mescroll实例对象
			upOption: {
				// page: {
				// 	num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
				// 	size: 10 // 每页数据的数量
				// },
				noMoreSize: 4, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
				empty: {
					use: true,
					icon: '../../static/images/no-data.png',
					tip: "抽奖尚在进行中，大奖在等你哦！",
					btnText: "去抽奖",
					fixed: true,
					top: "0",
					zIndex: 9999
				},
				textNoMore: '已全部加载',
			},
			pdList: [], //列表数据
			tabType: 0, // 菜单
			isShowFinish: false,
			apiName: 'my_going_choujiang_list'
		};
	},
	onShow() {
		this.mescroll.resetUpScroll(); // 刷新列表数据
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			// 来自页面内分享按钮
			console.log(res.target);
		}
		return {
			title: '大奖天天抽',
			path: '/pages/index/index'
		};
	},
	methods: {
		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			// 此时mescroll会携带page的参数:
			let pageNum = mescroll.num; // 页码, 默认从1开始
			let pageSize = mescroll.size; // 页长, 默认每页10条
			uni.request({
				url: getApp().globalData.apiUrl + 'api/pchoujiang/api',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: {
					page: pageNum,
					pageSize: pageSize,
					api_name: this.apiName,
					token: uni.getStorageSync('token')
				},
				success: data => {
					console.log(data.data);
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.data.data;
					// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
					let totalPage = data.xxx;
					// 接口返回的总数据量(比如列表有26个数据,每页10条,共3页; 则totalSize值为26)
					let totalSize = data.xxx;
					// 接口返回的是否有下一页 (true/false)
					let hasNext = data.xxx;

					//设置列表数据
					if (mescroll.num == 1) this.pdList = []; //如果是第一页需手动置空列表
					this.pdList = this.pdList.concat(curPageData); //追加新数据

					// 成功隐藏下拉加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					// mescroll.endByPage(curPageData.length, totalPage);

					//方法二(推荐): 后台接口有返回列表的总数据量 totalSize
					//mescroll.endBySize(curPageData.length, totalSize);

					//方法三(推荐): 您有其他方式知道是否有下一页 hasNext
					//mescroll.endSuccess(curPageData.length, hasNext);

					//方法四 (不推荐),会存在一个小问题:比如列表共有20条数据,每页加载10条,共2页.
					//如果只根据当前页的数据个数判断,则需翻到第三页才会知道无更多数据
					//如果传了hasNext,则翻到第二页即可显示无更多数据.
					mescroll.endSuccess(curPageData.length);

					// 如果渲染的数据较复杂,可延时隐藏下拉加载状态: 如
					this.$nextTick(() => {
						mescroll.endSuccess(curPageData.length);
					});
				},
				fail: () => {
					// 失败隐藏下拉加载状态
					mescroll.endErr();
				}
			});
		},
		//点击空布局按钮的回调
		emptyClick(mescroll) {
			uni.switchTab({
				url: '../index/index'
			})
		},

		// 切换菜单
		changeTab(type) {
			if (this.tabType !== type) {
				this.tabType = type;
				this.isShowFinish = false;
				this.apiName = 'my_going_choujiang_list';
				if(type == 1){
					this.isShowFinish = true;
					this.apiName = 'my_over_choujiang_list';
				}
				this.pdList = []; // 在这里手动置空列表,可显示加载中的请求进度
				this.mescroll.resetUpScroll(); // 刷新列表数据
			}
		}
	}
};
</script>

<style lang="less">
.list{
	padding-top: 20rpx;
}
.top-warp {
	z-index: 9990;
	position: fixed;
	top: --window-top; /* css变量 */
	left: 0;
	width: 100%;
	height: 100rpx;
	image {
		height: 100%;
		z-index: -1;
		position: absolute;
		left: 0;
		top: 0;
	}
	.tip {
		font-size: 28rpx;
		height: 60rpx;
		line-height: 60rpx;
		text-align: center;
	}
	.nav {
		text-align: center;
		height: 89rpx;
		padding-top: 15rpx;
		view {
			display: inline-block;
			width: 286rpx;
			line-height: 89rpx;
			font-size: 30rpx;
			color: #ffffff;
			margin: 0 30rpx;
			padding-bottom: 15rpx;
			background-image: url(data:image/png;base64,);
			background-size: 100% 100%;
		}
		.active {
			background-image: url(data:image/png;base64,);
		}
	}
}
</style>
